let fibonacci = function(){
    let cache =[0, 1];
    let fib = function(index){
        let result = cache[index]
        if(typeof result!=='number'){
            result = fib(index-1) + fib(index-2)
        }
        return result;
    }
    return fib;
}();

import {test} from 'ava'
test(t=>t.is(fibonacci(5), 5))